export default "import React from 'react';\nimport PropTypes from 'prop-types';\nimport classNames from 'classnames/bind';\nimport styles from './Overlay.module.scss';\n\nconst cx = classNames.bind(styles);\n\nconst propTypes = {\n  /**\n  * The overlay and the content to be displayed within the overlay.\n  */\n  overlay: PropTypes.node,\n  /**\n  * The elements that overlay should hide when overlay isOpen.\n  * elements which are not included in overlay will be wrapped within children for better use of accessibility.\n  */\n  children: PropTypes.node,\n};\n\nconst defaultProps = {\n  overlay: null,\n  children: null,\n};\n\nconst OverlayContainer = ({ overlay, children, ...customProps }) => {\n  const OverlayContainerClassNames = cx([\n    'overlay-container',\n    customProps.className,\n  ]);\n\n  return (\n    <div {...customProps} className={OverlayContainerClassNames}>\n      {overlay}\n      <div data-terra-overlay-container-content className={cx('container-content')}>\n        {children}\n      </div>\n    </div>\n  );\n};\n\nOverlayContainer.propTypes = propTypes;\nOverlayContainer.defaultProps = defaultProps;\n\nexport default OverlayContainer;\n"